body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}

/* font-family: 'Allison', cursive;
font-family: 'Josefin Sans', sans-serif;
font-family: 'Poppins', sans-serif;
font-family: 'Spartan', sans-serif; */

body {
  overflow-x: hidden;
  background-image: linear-gradient(
      0deg,
      rgba(100, 100, 100, 0.25),
      rgba(100, 100, 100, 0.25)
    ),
    url(/PrathimaPramod/static/media/blue1.d7b44f03.jpg);
  background-color: aliceblue;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
}

* {
  font-family: "Poppins", sand-serif;
}

#Header {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  color: aliceblue;
  z-index: 2;
  padding-top: 01%;
  padding-bottom: 01%;
}

#Header .headNav,
#Header .burger {
  display: none;
  z-index: 1;
}

#Header a {
  padding-left: 03%;
  padding-right: 03%;
  text-decoration: none;
  color: inherit;
  font-weight: 500;
  font-size: 20px;
  font-family: "Josefin Sans", sans-serif;
  transition: color 0.5s;
  text-shadow: 0 0 4px rgb(70, 40, 0), 0 0 3px rgb(70, 40, 0),
    0 0 2px rgb(70, 40, 0);
  transition: all 0.3s;
}

#Header a:hover {
  color: rgb(0, 0, 0);
  text-shadow: 0 0 3px aliceblue, 0 0 2px aliceblue;
}

#Header .headerMid {
  font-family: "Allison", cursive;
  font-size: 60px;
  font-weight: 400;
}

#Header .nav-links {
  display: flex;
  position: absolute;
  right: 0%;
  top: 0%;
  padding-top: 60px;
  padding-bottom: 110px;
  height: 100vh;
  width: 75%;
  background-color: rgba(0, 140, 255, 0.9);
  z-index: 1;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  transform: translateX(100%);
  transition: all 0.5s ease-in-out;
}
#Header .nav-active {
  transform: translateX(0%);
  box-shadow: 0 0 200px rgb(70, 40, 0), 0 0 150px rgb(70, 40, 0),
    0 0 100px rgb(70, 40, 0);
}
#Header .nav-links li {
  list-style: none;
  opacity: 0;
}
#Header .burger div {
  width: 25px;
  height: 3px;
  background-color: aliceblue;
  margin: 5px;
  transition: all 0.3s ease;
  box-shadow: 0 0 4px rgb(70, 40, 0), 0 0 3px rgb(70, 40, 0),
    0 0 2px rgb(70, 40, 0);
}

#Header .toggle .line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}
#Header .toggle .line2 {
  opacity: 0;
}
#Header .toggle .line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}

#Header .icons {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
}
#Header .icons a {
  color: aliceblue;
  font-size: 1.7rem;
  filter: drop-shadow(0 0 4px rgb(70, 40, 0));
}

@media only screen and (max-width: 768px) {
  #Header .headNav,
  #Header .burger {
    display: block;
  }
  #Header {
    justify-content: space-around;
  }
  #Header > a {
    display: none;
  }
  #Header .headerMid {
    display: block;
    font-size: 45px;
  }
}

@keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

#Footer {
  display: flex;
  width: 100%;
  margin-top: 3%;
  padding: 2%;
  background-color: rgba(173, 216, 230, 0.8);
  justify-content: center;
  align-items: center;
}

#Footer a {
  text-decoration: none;
  color: black;
  text-shadow: 0 0 3px aliceblue, 0 0 2px aliceblue;
  font-size: 1.6rem;
  margin-left: 10px;
  margin-right: 10px;
  transition: all ease 0.2s;
  filter: drop-shadow(0 0 4px aliceblue);
}
#Footer a:hover {
  color: aliceblue;
  filter: drop-shadow(0 0 5px blue);
}
#Footer .insta:hover {
  filter: drop-shadow(0 0 5px red);
}
#Footer .whatsapp:hover {
  filter: drop-shadow(0 0 5px green);
}

#Home {
  height: 90vh;
  padding-top: calc(2% + 60px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 10%;
  color: aliceblue;
  text-shadow: 0 0 4px rgb(70, 40, 0), 0 0 3px rgb(70, 40, 0),
    0 0 2px rgb(70, 40, 0);
}

#Home * {
  margin-top: 1.5%;
  margin-bottom: 1.5%;
}

#Home h1 {
  width: 51%;
}

#Home p {
  font-size: large;
}

#Home a {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

#Home button {
  padding: 10px;
  padding-left: 15px;
  padding-right: 15px;
  background-color: aliceblue;
  border-radius: 15px;
  border-width: 0;
  transition: background-color 0.5s;
  font-size: medium;
  box-shadow: 0 0 5px lightblue, 0 0 3px lightblue;
  transition: all 0.5s;
}

#Home button:hover {
  color: aliceblue;
  background-color: black;
}

@media only screen and (max-width: 768px) {
  #Home {
    padding-top: 0%;
    height: 100vh;
  }
  #Home * {
    margin-top: 7%;
    margin-bottom: 7%;
  }
  #Home h1 {
    width: 70%;
  }
}

#About {
