}

#About .aboutMe {
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 100vh; */
}

#About .aboutMe div {
  width: 70%;
  padding: 05%;
  background-color: rgba(240, 248, 255, 0.9);
}

#About .aboutMe p {
  font-size: large;
}

#About .aboutEdu {
  display: flex;
  align-items: center;
  justify-content: center;
  /* height: 60vh; */
  overflow: hidden;
  padding-top: 10%;
  padding-bottom: 10%;
}

#About .aboutEdu div {
  width: 40%;
