@media only screen and (max-width: 768px) {
  #About {
    padding-left: 2%;
    padding-right: 2%;
  }
  #About .aboutMain .imgCont {
    bottom: 15%;
    left: 32%;
    height: 70%;
    width: 65%;
  }
  #About .aboutMain .textCont {
    margin-top: 35%;
    margin-left: 0%;
    width: 75%;
  }
  #About .aboutMe div {
    width: 90%;
    padding: 03%;
  }
  #About .aboutMe p {
    font-size: small;
  }
  #About .aboutEdu h1 {
    font-size: medium;
  }
  #About .aboutEdu div {
    width: 60%;
  }
  #About .eduHead {
