#Testimonials {
  padding-top: calc(2% + 70px);
  padding-left: 07%;
  padding-right: 07%;
}

#Testimonials > h1 {
  padding-top: 5%;
  color: aliceblue;
  text-shadow: 0 0 4px black, 0 0 3px black, 0 0 2px black, 0 0 1px black;
}

#Testimonials p {
  font-size: small !important;
}

#Testimonials .col {
  /* background-color: black; */
  display: flex;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
  margin-top: 25px;
  margin-bottom: 25px;
}

#Testimonials .col .card {
  height: 300px;
  width: 250px;
  overflow: hidden;
  background-color: black;
  border-radius: 20px;
  color: aliceblue;
  text-shadow: 0 0 3px black, 0 0 2px black, 0 0 1px black;
  display: flex;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
}

#Testimonials .col img {
  height: 310px !important;
  width: auto !important;
  object-fit: cover !important;
  transition: all ease 0.3s;
  box-sizing: border-box;
}

#Testimonials .col .card:hover img {
  height: calc(100% + 80px) !important;
}

@media only screen and (max-width: 768px) {
  #Testimonials .col {
    width: 40vw;
    padding: 0% !important;
    z-index: -1;
  }
  #Testimonials .col .card {
    height: 200px;
    width: 40vw;
  }
  #Testimonials .col img {
    height: 205px !important;
    width: auto !important;
  }
}

#Book {
  padding-top: calc(2% + 100px);
  padding-left: 7%;
  padding-right: 7%;
}
#Book h1 {
  color: aliceblue;
  margin-bottom: 40px;
  text-shadow: 0 0 4px black, 0 0 3px black, 0 0 2px black;
}
#BookIn {
  display: flex;
  justify-content: center;
  /* width: 100%; */
}
#Book label {
  font-weight: 500;
}
.BookContact {
  width: 35%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  text-shadow: 0 0 3px aliceblue, 0 0 2px aliceblue;
}
.BookContact div {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 7px;
  padding: 0;
}
.BookContact span {
  background-color: rgba(240, 248, 255, 0.9);
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding: 30px;
  letter-spacing: 2px;
  font-size: 15px;
}
.rule {
  width: 1px;
  box-shadow: 0 0 5px grey, 0 0 2px grey;
  background-color: rgb(45, 45, 45);
  border-radius: 2px;
  margin-left: 7%;
  margin-right: 7%;
}
.BookForm {
  width: 35%;
}

@media only screen and (max-width: 768px) {
  #BookIn {
    flex-direction: column;
    align-items: center;
  }
  .BookContact span {
    font-size: small;
    padding: 20px;
  }
  .rule {
    display: none;
  }
  .BookForm {
